<template>
    <div>
        <i @click="likeClick()" class="fa fa-heart like-icon"></i>
    </div>
</template>

<script>
import axios from 'axios';
export default {
    data(){
        return {
            like: {
                users: [],
                count: 0,
                state: false,
                auction_id: 0,
                consumer_id: 0,
            }
        }
    },

    // checkWish -> get, 상품번호, auction_id, consumer_id
    async mounted(){
        this.like.auction_id = parseInt(this.$route.params.id)
        this.like.consumer_id = this.$store.state.login.userInfo.consumer_id;
        // alert(parseInt(this.$route.params.id));
    // axios.get(`/api/searchAuction/${this.checkUser}/${this.id}/${keyword}`)
           await axios.get(`/api/checkWish/${this.like.auction_id}/${this.like.consumer_id}`)
           .then(res => {
                alert('res!!', res);
                
            }).catch(err => {alert(err)}); 
    },
    methods: {
        likeClick(){
            if(this.like.state === true){
                axios.post('/api/registWish', {
                    auction_id: this.like.auction_id,
                    consumer_id: this.like.consumer_id
                }).then(() => {
                    alert('좋아요를 클릭했습니다!')
                }).catch(err => {console.log(err)});
            }else{
                axios.delete('/api/deleteWish', {params: {
                    auction_id: this.like.auction_id,
                    consumer_id: this.like.consumer_id
                }}).then(() => {
                    alert('좋아요를 취소하였습니다!')
                }).catch(err => console.log(err))
            }
        }
    }
}
</script>

<style lang="scss" scoped>
.like-icon{
    font-size: 25px;
    cursor: pointer;
    color: rgb(225, 225, 225);
}
</style>